@import '../mixins.scss';
@import '../variables.scss';

@mixin topNav {
  @include nested-grid;

  color: $grey;
  background-color: $ash;

  .top {
    @include grid-item;

    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 2.4rem;
    padding: 0.6rem 0;
  }

  .item {
    width: fit-content;
    display: flex;

    a {
      width: fit-content;
      height: fit-content;
      display: flex;
    }

    svg {
      width: 2.4rem;
      fill: $grey;
    }
  }

  @include hide;

  @media screen and (min-width: 768px) {
    @include show;
  }
}

@mixin mainNav {
  @include grid-item;

  display: flex;
  justify-content: space-between;
  padding: 1.2rem 0;

  .logoBox {
    display: flex;
    align-items: center;
    gap: 0.6rem;
    font-size: 2.4rem;

    .logoText {
      display: flex;
      flex-direction: column;
      justify-content: center;

      h5 {
        font-weight: 700;
      }

      .red {
        color: $primary-color;
      }
    }

    svg {
      width: 5rem;
      fill: $black;
      filter: drop-shadow(2px -1.5px 0 $primary-color);
    }
  }

  .main {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex: 1;
    color: lighten($ash, 20%);
    font-size: 1.8rem;
    gap: 4.8rem;
    padding: 0 6rem;
  }

  .getTicket {
    background-color: $white;
    border: 5px solid $primary-color;
    color: $primary-color;
    font-size: 1.8rem;
    font-weight: 700;
  }

  @include hide;

  @media screen and (min-width: 768px) {
    @include show;
  }
}

.header {
  @include grid;

  font-weight: 600;

  .topNav {
    @include topNav();
  }

  .mainNav {
    @include mainNav();
  }

  @media screen and (min-width: 768px) {
    .topNav {
      @include grid;

      padding: 0.6rem 0;

      .top {
        @include grid-item;
      }
    }
  }
}

.mobileNav {
  @include grid-item;

  padding: 1.2rem 2.4rem;
  position: absolute;
  width: 100vw;

  svg {
    fill: $ash;
  }

  @media screen and (min-width: 768px) {
    @include hide;
  }
}

.openNav {
  height: 100vh;
  width: 100vw;
  z-index: 100;
  flex-direction: column;
  justify-content: center;
  position: absolute;

  .topNav {
    @include topNav();
    @include show();

    flex-direction: column;
    width: 100vw;
    position: absolute;
    bottom: 0;

    .top {
      justify-content: center;
    }
  }

  .mainNav {
    @include mainNav();
    @include show();

    flex-direction: column;
    align-items: center;
    width: 100%;
    position: absolute;
    top: 4.8rem;
    padding: 2.4rem;

    .logoBox {
      justify-content: center;
      gap: 1.2rem;

      .logoText {
        align-items: center;
      }
    }

    .main {
      flex-direction: column;
      border: 5px solid white;
      width: 100%;
      gap: 2.4rem;
      margin: 2.4rem 0;

      li {
        border: 1px solid $primary-color;
        width: 85vw;
        text-align: center;
        padding: 1.2rem;
        font-weight: 700;
      }
    }

    button {
      width: 100%;
      padding: 1.2rem;
    }
  }

  .mobileNav {
    width: 100vw;
    position: absolute;
    padding: 1.2rem;
    display: flex;
    justify-content: center;
    background-color: $primary-color;

    svg {
      fill: $white;
    }
  }
}
